/*------------about------------*/
@import "global/vars";
@import "base/mixins";

.about {
  position: relative;
  z-index: 2;
  margin-bottom: 42px;
  padding-top: 21px;
  backdrop-filter: blur(2px);

  &__container {
    @include container();

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 62px;
    color: $white;
    text-align: center;

    @include respond-below(md) {
      font-size: 48px;
    }

    @include respond-below(md) {
      font-size: 30px;
    }
  }

  &__text {
    max-width: 745px;
    text-align: center;
    margin: 38px 0 38px 0;
    font-size: 18px;
    line-height: 23px;
    color: $white;
  }

  &__contacts {
    display: flex;
    flex-wrap: wrap;
    width: 77%;

    @include respond-above(md) {
      justify-content: space-around;
    }

    @include respond-below(md) {
      justify-content: center;
      align-items: center;
    }
  }

  &__contacts-items {
    text-align: center;
    margin: 20px 20px;
  }

  &__contacts-photo {
    width: 160px;
    height: 200px;
  }

  &__contacts-photo img {
    width: 100%;
    height: 100%;
    border-radius: 20%;
  }

  &__contacts-title {
    text-align: center;
    padding-top: 8px;
    font-weight: 700;
    font-size: 18px;
    line-height: 23px;
    color: $white;
  }

  &__contacts-text {
    font-weight: 400;
    line-height: 23px;
    color: $white;

    @include respond-above(md) {
      font-size: 13px;
    }

    @include respond-below(md) {
      font-size: 12px;
    }
  }
}
